import React from 'react'
import './contact.css'
import Separator from '../../common/separator/index'
import SocialContact from '../../common/social-contact/index';
import Zoom from 'react-reveal/Zoom';

function Contact() {
    return (
        <div className='contact'>
            <Separator />
            <label className='section-title'>Contact</label>
            <label className='section-title'></label>
            <div className='contact-container'>
                <div className='contact-left'>
                <Zoom right cascade><p>Want to hire me or get in touch? Contact me on any of the platform</p></Zoom>
                    <SocialContact />
                </div>
                <div className='resume'>
                    <a href={require('../../../assets/resume.pdf')} target='_blank' rel="noopener noreferrer">
                    <i class="fi-rr-download download-icon"></i> Download Resume</a>
                </div>
            </div>
        </div>
    )
}

export default Contact
