import React from 'react'
import './mobile.css'

function Mobile({isOpen, setisOpen}) {
    return (
        <div className='mobile'>
            <div className='close-icon' onClick={()=>setisOpen(!isOpen)}>
                <img src="https://img.icons8.com/material-rounded/24/000000/cancel--v2.gif" alt=''/>
            </div>
            <div className='mobile-options'>
                <div className='mobile-option'>
                    <a href='#projects' onClick={()=>setisOpen(!isOpen)}>
                    <img src="https://img.icons8.com/ios/50/000000/home-office--v2.gif" alt=''/>&nbsp;Projects
                    </a>
                </div>
                <div className='mobile-option'>
                    <a href='#skills' onClick={()=>setisOpen(!isOpen)}>
                    <img src="https://img.icons8.com/ios/64/000000/mind-map--v2.gif" alt=''/>&nbsp;Skills
                    </a>
                </div>
                <div className='mobile-option'>
                    <a href='#certificate' onClick={()=>setisOpen(!isOpen)}>
                    <img src="https://img.icons8.com/pastel-glyph/64/000000/scroll--v2.gif" alt=''/>&nbsp;Certificate
                    </a>
                </div>
                <div className='mobile-option' onClick={()=>setisOpen(!isOpen)}>
                    <a href='#contact'>
                    <img src="https://img.icons8.com/ios/50/000000/user--v3.gif" alt=''/>&nbsp;Contact
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Mobile
