import React from 'react'
import SocialContact from '../../common/social-contact'
import './about.css';
import Bounce from 'react-reveal/Bounce';

function About() {

    return (
        <div className='about'>
            <div className='about-top'>
                <Bounce bottom left>
                    <div className='about-info'>
                        Hello there, I am <br/>
                        <span className='info-name' id='info-name'>Sarath Adhithya</span><br/><br/>
                        I Love Web-Developing and I am a<div className='typing' id='typing'>FULL STACK DEVELOPER.</div>
                    </div>
                </Bounce>
                <Bounce bottom right>
                    <div className='about-photo'>
                        <img src={require('../../../assets/sarath.png')} className='img' alt=''/>
                    </div>
                </Bounce>
            </div>
            <SocialContact/>
        </div>
    )
}

export default About
