import React from 'react'
import './darkmode.css'

function Darkmode() {
    function dark_lite() {
        var element = document.getElementById("body");
        var element2 = document.getElementById("info-name");
        var element3 = document.getElementById("typing");

        element.classList.toggle("dark-mode");
        element2.classList.toggle("info-name-dark");
        element3.classList.toggle("typing-dark");
        
        let width = document.getElementById("body").offsetWidth;
        
        if(width > 780){
            
            document.getElementById("sun").classList.toggle("dark-icon-white");
            document.getElementById("msg2").classList.toggle("msg2-js");
        }
        else{
            document.getElementById("sun").classList.toggle("dark-icon-white");
        }
    }
    return (
        <div className='dark-icon'>
            <button className='btn' onClick={dark_lite}><i class="fi-rr-sun" id='sun'></i></button>
            <p className='msg1' id='msg1'>Dark Mode</p>
            <p className='msg2' id='msg2'>Light Mode</p>
        </div>
    )
}

export default Darkmode
