import React, { useState } from 'react'
import './project-card.css';
import Zoom from 'react-reveal/Zoom';
import Bounce from 'react-reveal/Bounce';
import Rotate from 'react-reveal/Rotate';

function ProjectCard({project}) {

    const [show, setShow] = useState(false);

    function showFunc(event)
    {
        let id = event.target.id;
        document.getElementById(id).style.display = "none";
        setShow(!show);
    }

    function startFromFirst(id){
        var video = document.getElementById(id);
        video.currentTime = 0;
    }

    return (
        <div className='project-card'>
            <div className='project-info'>
                <label className='project-title'>{project.title}</label>
                <Zoom left cascade>
                <div className='project-links'>
                    {project.demo && (          //if demo link is present then it will show else if it is empty then it will not show anything
                    <a className='project-link' href={project.demo} target='_blank' rel="noopener noreferrer">
                        <div className='link-button'>
                            <i class='fi-rr-globe'></i>Demo
                        </div>
                        </a>
                    )}
                    {project.github && (        //if github link is present then it will show else if it is empty then it will not show anything
                        <a className='project-link' href={project.github} target='_blank' rel="noopener noreferrer">
                            <div className='link-button'>
                                <i className="devicon-github-original colored"></i>Github
                            </div>
                        </a>
                    )}
                </div>
                </Zoom>
                <input type="button" className='link-button2' id={project.id} onClick={showFunc} value="Show more" />
                
                {
                    show?<p>{project.about}</p>:null                    
                }
                <br/>
                <Rotate left cascade>
                <div className='project-tags'>
                    {project.tags.map((tag)=>{
                        return(
                            <label className='tag'>{tag}</label>
                        )
                    })}
                </div>
                </Rotate>
            </div>
            <Bounce right delay={200}>
                <video className='project-video' alt='' loop autoPlay id={project.id+"video"} onClick={(ele)=> startFromFirst(ele.target.id)}>
                    <source src={project.image} type="video/mp4" />
                </video>     
            </Bounce>
        </div>
        
    )
}

export default ProjectCard
