import React from 'react'
import './footer.css'

function Footer() {
    function hoverin(){
        var heart = document.getElementById('heart');
        var react = document.getElementById('react');
        react.style.display = "none";
        heart.style.display = "block";
    }
    function hoverout(){
        var heart = document.getElementById('heart');
        var react = document.getElementById('react');
        react.style.display = "block";
        heart.style.display = "none";
    }

    return (
        <div className='footer' onMouseOver={hoverin} onMouseOut={hoverout}>
            Made with&nbsp;<i class="devicon-react-original-wordmark colored" id='react'></i><i class="fi-rr-heart heart" id='heart'></i>&nbsp;by _Sarath_
        </div>
    )
}

export default Footer
