import React from 'react'
import './scrollup.css'

function Scrollup() {
    function func(){
            var elem = document.getElementsByClassName("scroll-up")[0];   
            elem.classList.toggle("move");
    }
    return (
        <div className='scroll-up' id='scroll-up'>
            <a href='#top' onClick={func}>
                <img src={require('../../../assets/scrollup.png')} className='scroll-icon' alt='' />
            </a>
        </div>
    )
}

export default Scrollup
