import React from 'react'
import './home.css'
import Header from '../header/index'
import Body from '../body/index'
import Footer from '../footer/index'
import Scrollup from '../body/scroll-up/index'
import Darkmode from '../body/darkmode/index'


function Home() {
    return (
        <body className='body' id='body'>
            <div className='home' id='top'>
                <div>
                    <Header />
                </div>
                <div>
                    <Body />
                    <Darkmode />
                    <Scrollup />
                </div>
                <div>
                    <Footer />
                </div>
            </div>
        </body>
    )
}

export default Home
