import React from 'react'
import './web.css';
import Bounce from 'react-reveal/Bounce';

function Web() {
    return (
        <div className='web'>
            <div className='web-option'>
                <a href='#projects'>
                    <Bounce bottom cascade>
                        <img src="https://img.icons8.com/ios/50/000000/home-office--v2.gif" alt=''/>&nbsp;Projects
                    </Bounce>
                </a>
            </div>
            <div className='web-option'>
                <a href='#skills'>
                    <Bounce bottom cascade delay={500}>
                        <img src="https://img.icons8.com/ios/64/000000/mind-map--v2.gif" alt=''/>&nbsp;Skills
                    </Bounce>
                </a>
            </div>
            <div className='web-option'>
                <a href='#certificate'>
                    <Bounce bottom cascade delay={1000}>
                        <img src="https://img.icons8.com/pastel-glyph/64/000000/scroll--v2.gif" alt=''/>&nbsp;Certificate
                    </Bounce>
                </a>
            </div>
            <div className='web-option'>
                <a href='#contact'>
                    <Bounce bottom cascade delay={1500}>
                        <img src="https://img.icons8.com/ios/50/000000/user--v3.gif" alt=''/>&nbsp;Contact
                    </Bounce>
                </a>
            </div>
        </div>
    )
}

export default Web
