import React from 'react'
import './body.css'
import About from './about/index'
import Projects from './projects/index'
import Skills from './skills/index'
import Certificate from './certificate/index'
import Contact from './contact/index'

function Body() {
    return (
        <div className='body'>
            <section id='about'>
                <About />
            </section>
            <section id='projects'>
                <Projects />
            </section>
            <section id='skills'>
                <Skills />
            </section>
            <section id='certificate'>
                <Certificate />
            </section>
            <section id='contact'>
                <Contact />
            </section>
        </div>
    )
}

export default Body
